*{
    font-family: 'Netflix Sans','Helvetica Neue',Helvetica,Arial,sans-serif;
}*

.signup {    
    height: 100vh;
    overflow: hidden;
    width: 100%;
}
.signup-image{
    width: 100%;
    object-fit: cover;
    height: 100%;
}
.dark{
    position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    background:rgba(0, 0, 0, 0.8);
}
.nav{
    position: fixed;
    width: 100%;
    height: 103px;
    margin-top: 0px;
    float: left;
}
.logo-signup{
    position: fixed;
    width: 12rem;
    margin-top: 2rem;
    margin-left: 1rem;
}
.SIGNup{
    background-color: red;
    position: fixed;
    right: 11px;
    width: 6rem;
    height: 2rem;
    margin-top: 35px;
    font-weight: 400;
    font-size: 1rem;
    color: white;
    border: none;
    border-radius: 3px;
}

body .content p {
    text-decoration: none;
}
.body{
    width: 100%;
    height: 400px;
    margin-top: 103px;
}
.content{
    flex-direction: column;
    text-align: center;
    margin: auto;
    align-items: center;
}
#signup-title{
    color: #fff;
    font-size: 5vh;
    font-weight: bolder; 
}
#signup-title-2{
    color: #fff;
    font-size: 4vh;
    font-weight: 400; 
}
#signup-title-3{
    color: #fff;
    font-weight: 400;
}
.signup input{
    border: none;
    border-radius: 4px;
    font-size: medium;
    margin-bottom: 1rem;
    min-height: 3.5rem;
    outline-width: 0;
    padding-left: 35px;
    width: 67%;
}
.signin-button-start{
    background-color: red;
    border: none;
    color: #fff;
    font-size: 21px;
    height: 50px;
    vertical-align: top;
    width: 193px;

}

 .spinner{
    height: 18px;
    width: 18px;
    margin-left: 20px;
}